
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Step from 'vux/src/components/step/step.vue'
import StepItem from 'vux/src/components/step/step-item.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
name: 'CikeBaidu',
directives: {
TransferDom
},
components: {
Swiper,
SwiperItem,
Step,
StepItem,
Checker,
CheckerItem,
XInput,
Group,
Popup,
XDialog,
XButton
},
data () {
return {
swiperItemIndex: 0,
willClassList: [
{id: '1', willItem: ''},
{id: '2', willItem: ''},
{id: '3', willItem: ''},
{id: '4', willItem: ''},
{id: '5', willItem: ''},
{id: '6', willItem: ''},
{id: '7', willItem: ''},
{id: '8', willItem: ''},
{id: '9', willItem: ''},
{id: '10', willItem: ''}
],
willClassItem: [],
targetList: [
{id: '1', willItem: ''},
{id: '2', willItem: ''},
{id: '3', willItem: ''},
{id: '4', willItem: ''},
{id: '5', willItem: ''},
{id: '6', willItem: ''},
{id: '7', willItem: ''},
{id: '8', willItem: ''}
],
targetItem: null,
levelList: [
{id: '1', willItem: ''},
{id: '2', willItem: ''},
{id: '3', willItem: ''},
{id: '4', willItem: ''},
{id: '5', willItem: ''},
{id: '6', willItem: ''}
],
levelItem: null,
dayList: [
{id: '1', willItem: ''},
{id: '2', willItem: ''},
{id: '3', willItem: ''},
{id: '4', willItem: ''},
{id: '5', willItem: ''},
{id: '6', willItem: ''},
{id: '7', willItem: ''}
],
dayItem: null,
hoursList: [
{id: '1', willItem: '1'},
{id: '2', willItem: '2'},
{id: '3', willItem: '3'},
{id: '4', willItem: '4'},
{id: '5', willItem: '5'},
{id: '6', willItem: '6'},
{id: '7', willItem: '7'},
{id: '8', willItem: '8'}
],
hoursItem: null,
isShowVideo: false,
isShowYuyue: false,
isShowOffer: false,
isShowOfferTwo: false,
isShowDialog: false,
isTopTips: false,
topTipsText: '',
isClassList: false
}
},
watch: {
isTopTips (val) {
if (val) {
setTimeout(() => {
this.isTopTips = false
}, 2000)
}
}
},
filters: {},
mounted () {

},
computed: {},
methods: {
next () {
switch (this.swiperItemIndex) {
case 0:
if (this.willClassItem.length === 0) {
this.topTipsText = ''
this.isTopTips = true
} else { this.swiperItemIndex++ }
break
case 1:
console.log(this.targetItem)
if (this.targetItem === null) {
this.topTipsText = ''
this.isTopTips = true
} else { this.swiperItemIndex++ }
break
case 2:
console.log(this.levelItem)
if (this.levelItem === null) {
this.topTipsText = ''
this.isTopTips = true
} else { this.swiperItemIndex++ }
break
case 3:
console.log(this.dayItem, this.hoursItem)
if (this.dayItem === null || this.hoursItem === null) {
this.topTipsText = ''
this.isTopTips = true
} else { this.isShowOffer = true }
break
}
},
checkPlan () {
this.isShowOffer = false
this.isShowOfferTwo = true
}
}
}



